.header {
    padding: 32px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: #E5E5E5;
}

.header__title {
    margin: 0;
    padding: 0;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
}

.header__logout-container {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.header__logout-button {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: -0.408px;
    color: #41522E;
    text-decoration: none;
}

.header__logout-image {
    width: 18px;
    height: 18px;
    padding-left: 15px;
}

.main {
    display: grid;
    padding: 0 calc(100vw * 0.1361);
    grid-template-columns: calc(100vw * 0.25) 1fr;
    grid-template-rows: repeat(2, 1fr);
    gap: 24px;

    background-color: #E5E5E5;

}

.search {
    padding: 32px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background-color: white;
    box-shadow: 0px 4px 33px rgba(0, 0, 0, 0.04);
    border-radius: 16px;
}

.search__label {
    margin-bottom: 7px;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    color: #424242;
}

.search__input {
    height: 50px;
    margin-bottom: 16px;
    width: 100%;
    border: 1px solid #C9CACC;
    box-sizing: border-box;
    border-radius: 4px;
    outline: none;
}

.search__input::-webkit-calendar-picker-indicator {
    background-image: url(../../images/calendaricon.svg);
    background-size: 18px 18px;
    margin-right: 12px;
}

.search__input:last-of-type {
    margin-bottom: 32px;
}

.featured {
    padding: 32px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background-color: white;
    box-shadow: 0px 4px 33px rgba(0, 0, 0, 0.04);
    border-radius: 16px;
}

.featured__title {
    margin: 0;
    padding: 0;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    color: #424242;
}

.result {
    padding: 48px 32px 52px;
    grid-area: "result";
    background-color: saddlebrown;
    grid-row-start: 1;
    grid-row-end: 3;
    grid-column-start: 2;
    grid-column-end: 3;
    background-color: white;
    box-shadow: 0px 4px 33px rgba(0, 0, 0, 0.04);
    border-radius: 16px;
}

.result__title {
    margin: 0;
    padding: 0;
    font-style: normal;
    font-weight: 500;
    font-size: 32px;
    line-height: 37px;
    color: #424242;
}

.result__arrow {
    margin: 0;
    padding: 0;
    color: #A7A7A7;
}

.result__location {
    font-style: normal;
    font-weight: 500;
    font-size: 32px;
    line-height: 37px;
    color: #424242;
}