.login {
    height: 100vh;
    background-image: url(../../images/background-optimized.jpg);
    background-size: cover;
    background-position: center;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.login::before {
    content: '';
    background: rgba(255, 255, 255, 0.6);
    position: absolute;
    height: 100%;
    width: 100%;
}

.login__title {
    margin: 0 0 32px 0;
    padding: 0;
    text-align: center;
    position: relative;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    color: #424242;
    letter-spacing: -0.15rem;
}

.form {
    position: relative;
    display: flex;
    width: 409px;
    padding: 32px;
    background-color: white;
    flex-direction: column;
    justify-content: center;
    box-shadow: 0px 4px 33px rgba(0, 0, 0, 0.04);
    border-radius: 16px;
    box-sizing: border-box;
}

.form__input-name {
    padding-bottom: 7px;
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    color: #424242;
}

.form__input-data {
    height: 50px;
    margin-bottom: 14px;
    border: 1px solid #C9CACC;
    box-sizing: border-box;
    outline: none;
    border-radius: 4px;
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 18px;
}